*, *:before, *:after {
	box-sizing: border-box;
}

body {
	margin: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	height: 100vh;
	width: 100vw;
	overflow-x: auto;
	overflow-y: auto;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}


#root {
	height: inherit;
	width: inherit;
	overflow-x: inherit;
	overflow-y: inherit;
}

@media (min-width: 768px) {
	body {
		overflow-x: hidden;
	}
}